@import "variables";

.contact{
  .content-before{
    margin-bottom: 6rem;
  }

  #map{
    padding-top: 3.5rem;
    height: 90vh;
  }

  h2{
    font-size: 3.5rem;
    margin-bottom: 3.5rem;
  }

  .contact-item{
    margin-bottom: 2rem;
  }

  #emails{
    padding-bottom: 1.5rem;
  }

  #primary{
    .content-block{
      padding-right: 4.5rem;
    }
  }

  #secondary{
    .content-block{
      padding-left: 4.5rem;

    }
  }
  .content-block{
    padding-bottom: 5rem;
  }
}


#{$viewport-md-down}
{
  .contact {
    .content-block{
      padding: 0rem!important;
      margin-bottom: 4rem;
    }
  }
}