.filter{
  min-height: 5rem;
  line-height: 5rem;

  font-family: 'Gotham Book';
  font-size: 1.5rem;

  text-transform: uppercase;

  background: #ededed;

  .dropdown{
    display: none;
    position: absolute;
    width: 21.2rem;
    padding: 1rem 0;
    background: #f9f9f9;
    z-index: 999;
    a{
      display: block;
      width: 100%;
      background: #f9f9f9;
      padding-left: 5rem;
      font-family: 'Gotham Book';
      font-size: 1.5rem;
      line-height: 2.2rem;
      height: 2.2rem;
      color: #333333;
      text-decoration: none;
      overflow: hidden;
      &:hover{
        background: #f1f1f1;
      }
    }

  }

  .terms{
    position: relative;
    color: #333;
    text-decoration: none;
    width: 21.2rem;
    padding-left: 5rem;

    .dropdown{
      left: 0;
    }

    span{
      cursor: pointer;
      i{
        font-size: 1.8rem;
        margin-left: 0.5rem;
        height: 5rem;
        line-height: 5rem;
        float:right;

        transform: translate(50%, 0);
      }
    }

    &:hover{
      background: #f1f1f1;
      .dropdown{
        display: block;
      }
    }
  }

  .sort{
    position: relative;
    max-width: 21.2rem;
    span{
      cursor: pointer;
      border-left: 1px solid #d7d7d7;
      line-height: 1.5rem;
      font-size: 1.5rem;
      padding: 0.7rem 0;
      padding-left: 5rem;
      i{
        font-size: 1.8rem;
        margin-left: 0.5rem;
        height: 5rem;
        line-height: 5rem;
        float: right;
        transform: translate(50%, 0);
      }
    }

    .dropdown{
      right: 0;
    }
    
    &:hover{
      background: #f1f1f1;
      span{
        border: none;
      }

      .dropdown{
        display: block;
      }
    }
  }
}
