@import "variables";
.journals{
  background: #ededed;
  ul{
    padding: 0;
    margin: 0;
  }

  .content-before{
    background: #fff;
  }

  .content-body{
    padding-bottom: 4rem;
  }

  .journal-featured-image{
    max-height: 33rem;
    overflow: hidden;
    img{
      width: 100%;
    }
  }

  #featured{
    
    h1{
      font-family: 'DIN CondensedBold';
      text-transform: uppercase;
      font-size: 5.2rem;
    }

    .featured-journal-content{
      padding: 4rem 0;
      h1{
        margin-bottom: 4rem;
      }

      .journal-meta{
        font-family: 'Gotham Medium';
        .journal-meta-left{
          width: 20rem;
          border-right: 1px solid #ededed;
        }

        .journal-meta-right{
          padding-left: 5rem;
        }

        label{
          width: 7.8rem;
          font-family: 'Gotham Book';
          font-size: 1.5rem;
        }
      }

      .journal-excerpt{
        padding-left: 4.5rem;
        font-family: 'Gotham Book';
        font-size: 1.6rem;
        margin-bottom: 5rem;
      }
    }
  }

  .heading-results{
    min-height: 4.7rem;
    line-height: 4.7rem;
    background: #d7d7d7;

    span{
      padding: 0.4rem 0;
    }

    .results{
      padding-left: 5rem;
      padding-right: 2rem;
      border-right: 1px solid #fff;
      margin-right: 2rem;
    }

    .filters{
      .filter-title{
        margin-right: 2rem;
      }
    }

    .filter-close{
      text-decoration: none;
      padding: 1px 9px;
      background: url(../images/close.png) center center no-repeat;
    }
  }


  .journal{
    background: #fff;
    margin-bottom: 4rem;
    margin-top: 6rem;
    .content-block{
      padding: 2rem 5rem;

    }

    .journal-title{
      h2{
        font-family: 'DIN CondensedBold';
        text-transform: uppercase;
        color: #666666;
        font-size: 3rem;
        line-height: 3rem;
        margin-bottom: 3.3rem;
      }
    }

    .journal-excerpt{
      font-family: 'Gotham Book';
      font-size: 1.6rem;
      margin-bottom: 4rem;
    }
  }
}


#{$viewport-lg-down}{
  .journals{
    .journal-meta-left{
      border: 0!important;
    }
    .journal-meta-right{
      padding-left: 0!important;
    }
    .journal-excerpt{
      padding-left: 0!important;
    }
  }
}

.viewport-xs{
  .journals .heading-filter .sort span{
    border: none;
  }
}
