.product-show{
  position: relative;

  .product-marker{
    position: absolute;
    width: 36px;
    height: 36px;
    background: url(../images/product-marker.png) no-repeat;

    cursor: pointer;

    transform: translate(-50%, -50%);
  }

  .product{
    display: none;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 340px;
    background: rgba(247,247,247,0.81);

    .product-image{
      margin: 1.9rem 1.8rem;
    }

    .product-title{
      a{
         font-size: 3rem;
          text-transform: uppercase;
          font-family: 'DIN CondensedBold';
          text-decoration: none;
          color: #333332;
      }
     
      padding: 0 0.8rem;
      margin-bottom: 1.5rem;
      line-height: 3rem;
    }

    .product-description{
      font-family: 'Gotham Book';
      font-size: 1.6rem;
      padding: 0 0.8rem;
      margin-bottom: 1.5rem;
      line-height: 1.6rem;
    }

    .product-price{
      font-family: 'Gotham Book';
      font-size: 1.6rem;
      padding: 0 0.8rem;
      margin-bottom: 5rem;
      line-height: 1.6rem;
    }
  }
}