@import "variables";
.single-gallery{
  .swiper-slide{
    .image{
      max-height: 100vh;
      overflow: hidden;
    }
    img{
      max-width: 100%;
    }
  }

  #gallery-slider{
    padding-bottom: 1.5rem;
    .swiper-button-next{
      margin-right: 3rem;
    }

    .swiper-button-prev{
      margin-left: 3rem;
    }
  }

  #gallery-thumbnails{
    margin-left: -0.75rem;
    margin-right: -0.75rem;

    margin-bottom: 4rem;
    .image{
      margin: 0 0.75rem 1.5rem 0.75rem;
      max-height: 20rem;
      overflow: hidden;

      &.active{
        opacity: 0.5;
      }

      img{
        width: 100%;
        cursor: pointer;
      }
    }
    
    .row:last-child{
      .image{
        margin-bottom: 0;
      }
    }
  }

  .block-expand{
    box-sizing: content-box;
  }

  #gallery-features{
    margin-bottom: 10rem;
    ul{
      padding: 0;
      margin: 0;
      font-family: "Helvetica Neue";
      font-size: 1.533rem;

      &.features-left{
        margin-right:6.1rem;
      }

      &.features-right{
        margin-left:6.1rem;
      }

      li{
        line-height: 3.3rem;
        padding-left: 5rem;
        i{
          margin-right: 2.4rem;
          
          font-size: 0.8rem;
          line-height: 3.3rem;
        }
        &:nth-child(2n+1) { background: $color-acc-border-light;}
      }
    }
  }

  #gallery-title{
    h2{
      text-transform: uppercase;
      font-size: 3.5rem;
      margin-bottom: 2.4rem;
    }
  }


  #gallery-next{
    margin-bottom: 13rem;

    h2{
      font-size: 4.6rem;
      border-bottom: 4px solid $color-acc-border-light;
      padding-bottom: 2.2rem;
      margin-bottom: 4rem;
    }

    img{
      width: 100%;
    }

    .content-relative{
      max-height: 45rem;
      overflow: hidden;
      img{
        opacity: 0.7;
      }
    }

    .content-centered{

      max-width: 67rem;
      h1{
        font-size: 6rem;
        line-height: 1.2em;
        color: #fff;
        font-family: "DIN CondensedBold";
        margin-bottom: 7.5rem;
      }

      .next-link{
        a{
          font-family: "DIN CondensedBold";
          font-size: 2.8rem;
          line-height: 2.8rem;
          padding: 1.2rem 3.2rem;
          width: 22rem;
          i{
            margin-left: 3.5rem;
          }
        }
      }
    }
  }

  .actions{
    position: relative;
    height: 5rem;
    margin-bottom: 13rem;
  }
}