@import "variables";

.demo-bikes{
  .featured{
    min-height: 30vh;
    max-height: 33rem;
  }
  #heading{
    padding-top: 4.3rem;

    .heading-title{
      border-bottom: 3px solid $color-acc-border-light;
      h2{
        font-size: $size-acc-large;
        line-height: $size-acc-large;
        margin-bottom: 1.3rem;
      }

      p{
        font-family: 'Klavika Light';
        font-size: $size-acc-medium;
        line-height: $size-acc-medium;
        margin-bottom: 1.7rem;
      }
    }

    .heading-text{
      margin-top: 2.4rem;
      margin-bottom: 6.4rem;
      font-size: $size-acc-normal;
      font-family: 'Helvetica Neue';

      p{
        margin-bottom: 2.4rem;
      }

      .reserve-bike-link{
        font-family: 'Klavika LightItalic';
        font-size: 1.481rem;
        color: #7ac4d2;
        background: url(../images/icon-booking.png) left center no-repeat;
        padding-left:3rem;
      }
    }
  }


  .demo-bike{
    margin-bottom: 10rem;
    .bike-features{

    }

    .bike-title{
      h3{
        font-size: 3.2rem;
        line-height: 3.2rem;
        margin-bottom: 2.8rem;
        font-family: 'DIN CondensedBold';
        padding-left: 0.75rem;
      }
    }

    .bike-image{
      margin-bottom: 2rem;
      padding-right: 9rem;
    }

    .bike-book{
      a{
        font-size: 1.867rem;
        line-height: 3.5rem;
        min-width: 14.5rem;
        text-align: left;
        font-family: 'DIN CondensedBold';

        height: 3.5rem;
        padding: 0 1.8rem;


        i{
          float: right;
          font-size: 1.867rem;
          line-height: 3.5rem;

        }
      }
    }

    .bike-features{
      font-size: 1.533rem;
      font-family: 'Helvetica Neue';
      margin-bottom: 4.3rem;

      .bike-feature{
        line-height: 3.3rem;
        padding-left: 4.5rem;
        position: relative;
        
        i{
          font-size: 0.8rem;
          line-height: 3.3rem;
          position: absolute;
          left: 1.2rem;
        }

        &:nth-child(2n+1) { background: $color-acc-border-light;}
      }
    }
  }
}

#{$viewport-md-down}{
  .featured{
    min-height: 30vh;
    max-height: 33rem;
    img{
      height: 100%;
      width: auto;
      max-width: none;
    }
  }

  .bike-image{
    padding-right: 1rem!important;
  }
}