@import 'bower_components/components-font-awesome/scss/variables';
@import 'bower_components/components-font-awesome/scss/core';

.single-bike{
  .col-right{
    padding-left: 5rem;
  }

  .btn-zoom{
    margin-top: -8rem;
    width: 43px;
    height: 43px;
    background: url(../images/magnifier.png) no-repeat;
    outline: none!important;
  }

  .inquire{
    margin-top: 1.5rem;
    .btn{
      width: 284px;
    }
  }

  .tab-contents{
    background: #ededed;
  }

  ul.tabs
  {
    margin: 0px;
    padding: 0px;
    list-style: none;
    display: table;
    width: 100%;

    li{ 
      font-family: 'Gotham Medium';
      font-size: 1.5rem; 
      line-height: 1.5rem;
      color:#333332;
      text-transform: uppercase;
      cursor: pointer; 
      display: table-cell;

      &:last-child{
        margin-right: 0;
      }

      &.current{
        .tab-label .bottom-bar{
          display: block;
        }
      }

      .tab-label
      {
        padding-top: 1.5rem;
        padding-bottom: 3rem;
        display: inline-block;
        position: relative;

        span{
          text-decoration: underline;
        }

        .bottom-bar{
          display: none;
          position: absolute;
          width: 100%;
          bottom: 0;
          left: 0;
          border-top: 1px solid #ededed;
          border-bottom: 1rem solid #ededed;
          padding-bottom: 1rem;
          margin-top: 2rem;
        }
      }
    }
  }

  .tab-contents{
    font-size: 1.6rem; 
    font-family: 'Helvetica Neue';
    border:0; 
    line-height:19px; 
    color:#333332;
    background: #ededed;

    .tab-content{
      padding: 6rem 0 5rem 0;
      background: #ededed;
      display: none;
      line-height: 2.2rem;
      
      p{
        line-height: 2.2rem;
      }

      &.current{
        display: block;
      }

      .table-wrapper{
        margin-right: 5rem;
      }

      table{
        width: 100%;
        tr{
          border-bottom: 1px solid #e2e2e2;
          td{
            padding: 5px 0;
          }
        }
      }

    }

    ul{
      font-family: 'Helvetica Neue';
      list-style: disc;
      li{
        margin-bottom: 1.5rem;
        margin-left: 1.5rem;
        padding-left: 1.2rem;
        font-size: 1.6rem;
        line-height: 1.6rem;
      }
    }

    h1{
      font-size: 3.5rem; 
      margin-bottom: 3.5rem;
      font-family: 'DIN CondensedBold';
      text-transform: uppercase;
    }
    .spr-container{padding:0!important; border:0!important;}
    .spr-header-title{font-size: 3.5rem; text-transform: uppercase; line-height: 3.5rem; margin-bottom: 4rem;}
  } 

  #product-reviews{
    margin-bottom: -6rem;
    .items{
      padding: 0 5rem;
      padding-bottom: 7rem;
      font-family: 'Helvetica Neue';

      &.ac{
        border-right: 1px solid #999;
      }

      .item{
        border-bottom: 1px solid #999999;
        margin-bottom: 4rem;

        &:last-children{
          margin-bottom: 1.5rem;
        }
      }

      .view-all a{
        font-family: 'Gotham Medium';
        font-size: 1.5rem;
        color: #999999;
        i{
          padding-left: 4.5rem;
        }
      }
    }

    h2{
      font-family: 'DIN CondensedBold';
      text-transform: uppercase;
      color: #666666;
      margin-bottom: 3rem;
    }
    p{
      margin-bottom: 4rem;
    }
    a.btn{
      width: auto;
      padding-left: 3.8rem;
      padding-right: 3.8rem;
      i{
        padding-left: 4rem;
      }
    }
  }

  #lifestyle-slider{
    overflow: hidden;
    max-height: 66.3rem;
    background: #fff;
    img{
      max-width: 100%;
      width: 100%;
      height: auto;
    }
  }

  .swiper-buttons{
    height: 4.5rem;
    z-index: 99;
  }

  .swiper-pagination-bullets{
    bottom: 2rem!important;
    z-index: 99;
    font-size: 2rem;
    line-height: 2rem;
    .swiper-pagination-bullet{
      margin: 0 0.7rem;
      width: 1.2rem;
      height: 1.2rem;
      background: none;
      cursor: pointer;

      &:after{
        font: normal normal normal #{$fa-font-size-base}/#{$fa-line-height-base} FontAwesome; // shortening font declaration
        content: $fa-var-circle-o;
        font-size: 2rem;
        color: #aaa;
      }

      &.swiper-pagination-bullet-active:after{
        content: $fa-var-circle;
      }
    }
  }

  .swiper-button-prev, .swiper-button-next{
    background: #ededed;
    color: #808080;
    font-size: 3rem;
    line-height: 4.5rem;
    border-radius: 0.5rem;
    width: 4.5rem;
    height: 4.5rem;
    text-align: center;
    cursor: pointer;
  }

  .actions{
    height: 25rem;
  }
}