@font-face {
  font-family: 'DIN Mod';
  src:  url('../fonts/font-DIN-Mod.eot');
  src:  url('../fonts/font-DIN-Mod.eot') format('embedded-opentype'),
        url('../fonts/font-DIN-Mod.woff2') format('woff2'),
        url('../fonts/font-DIN-Mod.woff') format('woff'),
        url('../fonts/font-DIN-Mod.ttf') format('truetype'),
        url('../fonts/font-DIN-Mod.svg#DIN-Mod') format('svg');
}

@font-face {
  font-family: 'DIN CondensedBold';
  src: url("../fonts/font-DIN-Condensed-Bold.eot");
  src: url("../fonts/font-DIN-Condensed-Bold.eot") format("embedded-opentype"),
       url("../fonts/font-DIN-Condensed-Bold.woff") format("woff"),
       url("../fonts/font-DIN-Condensed-Bold.ttf") format("truetype"),
       url("../fonts/font-DIN-Condensed-Bold.svg#DIN-Condensed-Bold") format("svg");
  font-weight: normal;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

