
@import "variables";
.container-narrow{
  max-width: 75.4rem;
  margin: 0 auto;
}

.container-image{
  max-width: 124.1rem;
  margin: 0 auto;

  &::after {
    content: "";
    display: table;
    clear: both;
  }

  .image-left{
    width: 50%;
    box-sizing: 'content-box';
    padding-right: 0.7rem;
    float: left;
  }

  .image-right{
    width: 50%;
    box-sizing: 'content-box';
    padding-left: 0.7rem;
    float: left;
  }

  img{
    margin-bottom: 1.5rem;
  }
}

.container-wide{
  max-width: 132.3rem;
  margin: 0 auto;
}

.container-full{
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  img{
    width: 100%;
  }
}




.single-journal{
  .featured{
    margin-bottom: 2.6rem;
    max-height: 60vh;
  }

  .featured-title{
    padding: 3rem 3rem;
    max-width: 78rem;
    margin: 0 auto;
    text-align: center;
  }

  .journal-meta{
    border-bottom: 1px solid #ededed;
    font-family: 'Gotham Medium';
    padding-bottom: 1.5rem;
    margin-bottom: 6rem;
    .journal-meta-left{
      min-width: 20.6rem;
      border-right: 1px solid #ededed;
    }

    .journal-meta-right{
      padding-left: 5rem;
    }

    label{
      width: 7.8rem;
      font-family: 'Gotham Book';
      font-size: 1.5rem;
    }
  }

  .content-body{
    color: #333332;
    font-family: 'Helvetica Neue', 'Arial';
    font-size: 1.6rem;
    
    .container-narrow{
      max-width: 75.4rem;
      margin: 0 auto;
    }

    .container-image{
      max-width: 124.1rem;
      margin: 0 auto;

      &::after {
        content: "";
        display: table;
        clear: both;
      }

      .image-left{
        width: 50%;
        box-sizing: 'content-box';
        padding-right: 0.7rem;
        float: left;
      }

      .image-right{
        width: 50%;
        box-sizing: 'content-box';
        padding-left: 0.7rem;
        float: left;
      }

      img{
        margin-bottom: 1.5rem;
      }
    }

    .container-wide{
      max-width: 132.3rem;
      margin: 0 auto;
    }

    .container-full{
      width: 100%;
      max-width: 100%;
      margin: 0 auto;
      img{
        width: 100%;
      }
    }

    &>p, &>div{
      max-width: 80rem;
      margin: 0 auto;
      padding: 0 2rem;

      img{
        max-width: 100%;
        height: auto;
      }
    }

    h2{
      font-size: 2.9rem;
      text-transform: uppercase;
      margin-bottom: 2rem;
    }

    a{
      color: #e4344d;
      text-decoration: underline;
      font-family: 'Klavika MediumItalic';
    }

    ul{
      min-width: 16rem;
      margin-bottom: 6rem;

      li{
        line-height: 1.6rem;
        margin-bottom: 1.8rem;
      }

      i{
        width: 2.2rem;
        display: inline-block;
        font-family: 'Gotham Book';
        font-style: normal;
        font-weight: bold;
        line-height: 1.6rem;

        &.fa-circle{
          font-size: 0.8rem;
        }

        &.fa-minus{
          font-size: 0.8rem;
        }

        &.fa{
          font-family: 'FontAwesome';
          line-height: 1.6rem;
          vertical-align: middle;
          margin-top: -2px;
        }
      }
    }

    p{
      margin-bottom: 3rem;
      font-family: 'Helvetica Neue', 'Arial';
      font-size: 1.6rem;
      color: #333332;
      letter-spacing: 0.06rem;
    }

    .image-caption{
      max-width: 55rem;
      margin: 0 auto;

      font-family: 'Gotham Book';
      text-align: center;
      font-size: 1.6rem;
      color: #808080;
      padding-bottom: 3.5rem;
      margin-top: 3.8rem;
      margin-bottom: 6rem;

      .bottom-border{
        width: 72%;
        margin: 0 auto;
        margin-top: 4rem;
        border-bottom: 1px solid #e1e0dd;
      }
    }
  }

  .comments{
    margin-bottom: 8rem;
  }
}

.share-buttons{
  margin-bottom: 12rem;
  .share-this-story-label{
    font-family: 'Gotham Medium';
    font-size: 1.8rem;
    margin-bottom: 2rem;
  }

  .at-share-btn-elements{
    display: inline-block;
    padding: 2rem 0;
    border-top: 1px solid #d0d0d0;
    border-bottom: 1px solid #d0d0d0;

    .at-share-btn{
      background: none!important;
      padding: 0.9rem!important;
      margin: 0!important;
    }

    .at-label{
      display: none;
    }

    svg{
      fill: rgb(128, 128, 128)!important;
    }
  }
}

.journal-next{
  overflow: hidden;

  h2{
    border-bottom: 4px solid #f5f5f5;
    margin-bottom: 4rem;
    padding-bottom: 2rem;
    font-size: 4.6rem;
    line-height: 4.6rem;
  }

  img{
    height: 100%;
    width: auto;
  }

  .next-image{
    max-height: 66.2rem;
    overflow: hidden;
  }

  .next-title{
    font-size: 10.3rem;
    font-family: 'DIN CondensedBold';
    max-width: 110rem;
    width: 100%;
    margin: 0 auto;
    color: #fff;
    margin-bottom: 3rem;
  }

  .next-link{
    a{
      font-family: "DIN CondensedBold";
      font-size: 2.8rem;
      line-height: 2.8rem;
      padding: 1.2rem 3.2rem;
      width: 22rem;
      i{
        margin-left: 3.5rem;
      }
    }
  }
}

#{$viewport-lg-down}{
  .single-journal{
    .featured-title{
      font-size: 4rem;
    }

    .journal-next{
      .next-title{
        font-size: 5rem!important;
      }

      .next-image{
        height: 40vh;
        img{
          min-width: 100%;
          max-width: none;
        }
      }
    }
  }
}

#{$viewport-md-up}{
  .single-journal{
    .journal-next{
      .next-title{
        font-size: 8rem!important;
      }

      .next-image{
        height: 66.2rem!important;
        img{
          min-width: 100%;
          max-width: none;
          height: auto;
          width: auto;
        }
      }
    }
  }
}

#{$viewport-md-down}{
  .container-narrow, .container-image{
    padding:0 2rem;
  }

  .image-caption{
    margin-left: 2rem!important;
    margin-right: 2rem!important;
  }
}
