.galleries{
  .crumb:first-child{
    padding-left: 3.1rem;
  }

  .heading-results{
    height: 4.7rem;
    line-height: 4.7rem;
    background: #d7d7d7;

    span{
      padding: 0.4rem 0;
    }

    .results{
      padding-right: 2rem;
      border-right: 1px solid #fff;
      margin-right: 2rem;
    }

    .filters{
      .filter-title{
        margin-right: 2rem;
      }
    }

    .filter-close{

      text-decoration: none;
      padding: 1px 9px;
      background: url(../images/close.png) center center no-repeat;
    }
  }

  .content-body{
    background: #ededed;
    padding: 4rem 0;

    .gallery{
      background: #fff;
      padding: 2rem 5rem 4rem 5rem;
      margin-bottom: 4rem;

      .gallery-featured-image{
        margin-bottom: 4rem;
        max-height: 35rem;
        overflow: hidden;
        img{
          width: 100%;
        }
      }

      .gallery-title{
        h3{
          font-size: 3rem;
          line-height: 100%;
          font-family: 'DIN CondensedBold';
          text-transform: uppercase;
          color: #666666;
          margin-bottom: 4rem;
        }
      }

      .gallery-action{
        a{
          font-family: 'Gotham Medium';
          text-transform: uppercase;
          font-size: 1.5rem;
          line-height: 2rem;
          padding: 1.25rem 3.9rem;
          i{
            margin-left: 4rem;
            font-size: 2rem;
          }
        }
      }
    }
  }

  .actions{
    position: relative;
    padding-top: 10rem;
    height: 5rem;
    padding-bottom: 13rem;
  }
}

.viewport-xs{
  .galleries
  {
    .sort span{
      border: none!important;
    }
  }
}
