@import 'variables';
.swiper-slide img{
  width: 100vw;
}

.caption{
  max-width: 50rem;
  position: absolute;
  top: 50%;

  font-family: 'Gotham Medium';
  
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);

  color: #fff;
  font-size: 1.3rem;

  &.caption-left{
    left: 15vw;
  }

  &.caption-right{
    right: 15vw;
  }

  &.caption-center{
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .caption-heading, h2, h1{
    font-size: 6rem;
    line-height: 6rem;
    text-transform: uppercase;
    font-weight: normal;
    margin-bottom: 0;
  }

  .caption-text{
    font-size: 1.7rem;
    margin-bottom: 1.8rem;
  }

  .caption-link{
    a{
      font-family: 'Gotham Medium';
      color: #fff;
      font-size: 1.8rem;
      line-height: 4.4rem;
      text-decoration: none;
      height: 4.4rem;
      padding: 0 3.9rem;

      &:after{
        background-color: #7bc4d2;
      }
    }
  }
}

#home-slider{
  .swiper-button-next{
    background: url(../images/button-next.png);
  }

  .swiper-button-prev{
    background: url(../images/button-prev.png);
  }

  .swiper-pagination-bullets{
    width: auto!important;
    left: auto!important;
    right: 10vw!important;
    bottom: 5rem!important;

    .swiper-pagination-bullet{
      background: url(../images/bullet.png) center center no-repeat;
      width: 16px;
      height: 16px;
    }

    .swiper-pagination-bullet-active{
      background: url(../images/bullet-active.png) center center no-repeat;
      width: 16px;
      height: 16px;
    }
  }
  
  .swiper-container{
    background: #000;
  }
}

#{$viewport-lg-down}{
  #home-slider{
    img{
      width: auto!important;
      max-width: none!important;
      height: 100vh;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }

    .swiper-slide{
      height: 100vh;
      overflow: hidden;
    }
  }
}