@import "bower_components/bootstrap/scss/variables";
@import "bower_components/bootstrap/scss/mixins";
@import "variables";

@media only screen and (min-width: 768px){
  .container{
    max-width: 135.8rem!important;
  }

  .container-acc{
    padding: 0 5rem!important;
  }

	.container-acc{
		max-width: 132.8rem;
		padding: 0 5rem;
		margin: 0 auto;
	}	

	.block-acc-expand{
		margin-left: -5rem;
		margin-right: -5rem;
	}

	.block-acc-expand-right{
		margin-right: -5rem;
	}

	.block-acc-expand-left{
		margin-left: -5rem;
	}
}

@media only screen and (max-width: 1358px){
  .container{
    max-width: 100%!important;
  }

  .container-acc{
    margin: 0 2rem!important;
    padding: 0 2rem!important;
  }

  .block-acc-expand{
  	margin-left: -2rem!important;
  	margin-right: -2rem!important;
  }

  .block-acc-expand-right{
		margin-right: -2rem;
	}

	.block-acc-expand-left{
		margin-left: -2rem;
	}
}

@media only screen and (max-width: 1024px){
	html{
		font-size: 9.5px!important;
	}

	.container-acc{
    margin: 0 2rem!important;
  }

  .container-acc{
		padding: 0 2rem!important;
	}

	.block-acc-expand{
		margin: 0!important;
	}

	.block-acc-left{
		margin: 0!important;
	}

	.block-acc-right{
		margin: 0;
	}
}

@media only screen and (max-width: 768px){
	html{
		font-size: 9.5px!important;
	}
}

@media only screen and (max-width: 640px){
	html{
		font-size: 9px!important;
	}
}

@media only screen and (max-width: 480px){
	html{
		font-size: 9px!important;
	}
}

html{
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
}

#{viewport-md-down}{
	.container{
		width: 100%!important;
		max-width: none;
	}
}

article{
  padding: 2rem 0;
}

.container-acc-expand{
  max-width: 132.3rem;
  margin: 0 auto;
}

.block-expand{
	margin-left: -1.5rem;
	margin-right: -1.5rem;
}

.block-expand-left{
	margin-left: -1.5rem;
}

.block-expand-right{
	margin-right: -1.5rem;
}

.text-size-medium{
	font-size: $size-acc-medium!important;
}

.text-size-large{
	font-size: $size-acc-large!important;
}

.text-size-normal{
	font-size: $size-acc-normal!important;
}

::selection {
  background: $color-acc-sky;
  color: #fff;
}
::-moz-selection {
  background: $color-acc-sky;
  color: #fff;
}

body{
	font-family: 'Helvetica Neue', 'Arial';
	background: $color-acc-footer!important;
}

strong{
	font-family: 'Gotham Medium';
}

a{
	&:hover{
		cursor: pointer;
	}
}

ul{
	list-style: none;
  padding: 0;
  margin: 0;
}

h2{
	font-size: 3rem;
	font-family: 'DIN CondensedBold';
}

#site-branding{
	#site-logo{
		padding: 2rem;
		padding-bottom:1.2rem;
	}

	#logo{
		img{
			width: 6rem;
		}
	}
}

header{
	font-family: "Gotham Book";
}

.input-group.search{
	.input-group-addon{
		font-size: 1.6rem;
		padding-left: 2rem;
	}
	
	border: 1px solid #ededed;
	input{
		height: 3.5rem;
		font-size: 1.8rem;
		border: none;
	}
}

#search-form{
	margin-top: 3.5rem;
	margin-right: 3rem;
	margin-bottom: 1rem;
  width: 25.5rem;

  .input-group{
		@include border-radius(0.5rem);
	}
}

#site-navigation{
	font-size: 1.3rem;
	line-height: 5rem;

	border-top: 1px solid $color-acc-main-nav-border;
	border-bottom: 1px solid $color-acc-main-nav-border-bottom;
	padding: 0 3.5rem;

	#main-menu{
		text-align: center;
		a{
			font-size: 2.1rem;
			text-transform: uppercase;
			color: $color-acc-main-nav;
			padding: 0 1rem;
			font-family: 'DIN CondensedBold';
		}
	}

	#cart-links{
		a{ 
      font-size: 1.3rem;
      font-family: 'Gotham Medium';
      color: #8d8d8d;
		}
	}
}

nav{
	ul{
		padding: 0;
		margin: 0;

		li{
			margin: 0;
			padding: 0;
		}
	}
}

.site-footer{
	padding-top: 5rem;
	background: $color-acc-footer-bg;
	min-height: 15rem;
	font-size: 1.4rem;
	color: $color-acc-footer;
	
	.site-links{
		ul{ margin: 0; padding: 0; }
		li{ margin: 0; padding: 0; list-style: none; margin-bottom: 1.8rem; }
		a{

			font-size: 1.8rem;
			color: $color-acc-footer;
			font-family: 'DIN CondensedBold';
      
		}
	}
	.site-info{
		border-top: 1px solid #474747;
		margin-top: 4rem;
		padding: 1.8rem 0;

		.policy-links{
			margin-bottom: 1rem;
		}

		a{
			color: $color-acc-footer;

      padding-left: 6rem;
      &:first-child{
        padding-left: 0;
      }

		}
	}

  .social-links{
    margin-bottom: 2rem;
    a{
      i{
        display: inline-block;
        text-align: center;
        font-size: 2.5rem;
        width: 4rem;
        height: 4rem;
        line-height: 4rem;
        border-radius: 50%;
        color: #191919;
        background: #555;
      }

      margin-left: 2rem;
    }
    
  }
}

.collapse{
	overflow: hidden;
}

#toggle-menu-button{
	font-size: 2.2rem;
	color: #000;
	text-decoration: none;
	&:hover{
		text-decoration: none;
	}
}

#page{
	position: relative;
  overflow-x: hidden;
}

#bread-crumbs{
	min-height: 4.2rem;
	line-height: 4.2rem;
	text-transform: uppercase;

	.crumb{
		padding-right: 1rem;
	}

	.crumb-step1{
		font-family: 'Gotham Light';
	}

	.crumb-angle{
		font-family: 'Gotham Book';
	}

	.crumb-step2{
		font-family: 'Gotham Medium';
	}

  a{
    color: #333332;
    &:hover{
      color: #333332;
    }
  }
}

.icon-round{
	width: 5rem;
  height: 5rem;
  border-radius: 50%;
  display: block;
  background: #f0f0f0;
  text-align: center;
  font-size: 2.8rem;
  line-height: 5rem;
  color: #959595;
  text-decoration: none!important;
  i{
    height: 100%;
    display: block;
    line-height: 5rem;
  }

  &:focus{
  	color: #959595;
  }

  &:hover{
  	color: #fff;
  }

}

img{
	max-width: 100%;
}

a.btn-acc{
  color: #fff!important;
  font-family: 'Gotham Medium';
  text-transform: uppercase;
  font-size: 1.5rem;
  line-height: 2rem;
  padding: 1.1rem 3.9rem;
  i{
    margin-left: 4rem;
    font-size: 2rem;
  }
}

.featured{
  position: relative;
  overflow: hidden;
}

h1.featured-title{
  font-size: 7.2rem;
  font-family: 'DIN CondensedBold';
  text-transform: uppercase;
  color: #fff;
}

.content-relative{
  position: relative;
}

.content-full-width{
  width: 100%;
}

.content-bottom{
  position: absolute;
  bottom: 0;
}

#{$viewport-lg-down}{
  .content-centered-lg-down
  {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

#{$viewport-xl-down}{
  .content-centered-xl-down
  {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

#{$viewport-md-down}{
  .content-centered-md-down
  {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

#{$viewport-xl-down}{
  .featured.featured-xl-down{
    min-height: 40vh;
    img{
      height: 100%;
      width: auto;
      max-width: none;
    }
  }
}

#{$viewport-lg-down}{
  .featured.featured-lg-down{
    min-height: 40vh;
    img{
      height: 100%;
      width: auto;
      max-width: none;
    }
  }
}

#{$viewport-md-down}{
  .featured.featured-md-down{
    min-height: 40vh;
    img{
      height: 100%;
      width: auto;
      max-width: none;
    }
  }
}

.featured{
  img{
    width: 100vw;
  }
}


.center, .content-centered{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.center-horizontal, .content-centered-horizontal{
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
}

.center-vertical, .content-centered-vertical{
  position: absolute;
  top: 50%;
  transform: translate(0%, -50%);
  width: 100%;
}

#content{
	font-size: 1.5rem;
}

.border-acc-right{
  position: absolute; height: 100%; border-right: 1px solid #ACACA6; right:0;
}

.border-acc-left{
  position: absolute; height: 100%; border-right: 1px solid #ACACA6; left:0;
}

.pagination{
  padding: 4rem 0;

  a, span{
    font-family: 'Gotham Book';
    font-size: 1.5rem;
    line-height: 1.6rem;
    padding: 0 1rem;
    border-right: 1px solid #d7d7d7;
    color: #333332;
    &.current{
      color: #7bc4d2;
    }

    &:last-child{
      border: none;
    }
  }
}

#zoom{
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background: #fff;
  z-index: 999999;
  display: none;

  .btn-close-zoom{
    font-size: 2.6rem;
    text-decoration: none;
    color: #333;
  }

  .zoom-header{
    padding: 2rem 3rem;
  }

  .zoom-content{
    text-align: center;
  }
}

a{
  text-decoration: none;
  cursor: pointer;
  &:hover{
    text-decoration: none;
  }
}

a.btn{
  &:after{
    display: none;
  }
}

a.ac-link{
  @include ac-link;
}
