@import "variables";
.faq{
  
  #heading{
    background: #d7d7d7;
    .heading-menu{
      font-family: 'Gotham Book';
      min-height: 5rem;
      font-size: 1.5rem;
      line-height: 5rem;
      a{
        font-size: 1.5rem;
        line-height: 5rem;
        color: $color-acc-text;
        margin-left: 2.5rem;
        display: block;
        float: left;
      }
    }
  }

  .actions{
    margin-bottom: 13rem;
    position: relative;
  }


  .accordion{
    margin-bottom: 13rem;
    .panel{
      font-family: 'Klavika Light';
      border-bottom: 0.2rem solid $color-acc-border-light;
      margin: 0 -1rem;
      padding: 1rem;
      .panel-collapse{
        max-width: 76rem;
      }
    }

    h2{
      a{
        color: $color-acc-text;
        font-size: 3.505rem;
        line-height: 3.505rem;
        display: block;
        max-width: 55rem;
        padding: 4rem 0;

        i.fa-caret-up{
          display: none;
          color: #7bc4d2;
        }
        i.fa-caret-down{
          display: block;
          color: #acacac;
        }

        &[aria-expanded=true]{
          i.fa-caret-up{
            display: block;
          }
          i.fa-caret-down{
            display: none;
          }
        }
      }
      
    }

    h3{
      font-size: 2rem;
      text-transform: uppercase;
      font-family: 'Gotham Medium';
    }

    p{
      margin-bottom: 3.5rem;
      font-size: 1.6rem;
      font-family: 'Gotham Book';
      a{
        font-family: 'Klavika MediumItalic';
        color: $color-acc-pink;
      }
    }
  }
}

#{$viewport-md-down}
{
  .faq{
    .featured{
      height: 40vh;
      
      img{
        width: auto!important;
        max-width: none;
        height: 100%;
        width: auto!important;
        max-width: none;
      }
    }  
  }
}

#{$viewport-md-up}
{
  .faq{
    .featured{
      min-height: 25rem;
      max-height: 55rem;
      img{
        width: 100%;
        max-width: none;
        max-width: none;
      }
    }  
  }
}