@import "variables";

.bikes{
  #page-title{
    border-bottom: 3px solid #f5f5f5;
    font-family: 'Gotham Light';
    font-size: 1.267rem;
  }

  .filter{
    background: #fff;
    font-family: 'Gotham Book';
    font-size: 1.5rem;
    line-height: 3.5rem;
    margin-top: 3.2rem;
    margin-bottom: 4.5rem;

    h3{
      margin-bottom: 1rem;
      font-family: 'Gotham Medium';
    }

    a{
      color: #333332;
    }

    .categories{
      .list{
        span{
          padding: 0.8rem 2rem;
          border-right: 1px solid #e1e0dd;
          &:first-child{
            padding-left: 0;
          }
          &:last-child{
            border: none;
          }
        }
      }

      &.terms{
        border: 1px solid #333;
        padding-left: 2rem;
        i{
          height: 3.5rem;
          line-height: 3.5rem;
          float: right;
          transform: none;
          margin-right: 2rem;
        }

        a{
          padding-bottom: 1rem;
          height: 3rem;
          line-height: 3rem;
        }
      }
    }
    
  }

  .featured{
    font-family: 'Gotham Book';
    font-size: 1.8rem;
    color: #fff;

    .featured-content{
      max-width: 31.5rem;
      margin-left: 8rem;
    }

    p{
      margin-bottom: 2.5rem;
    }

    h1{
      font-family: 'DIN CondensedBold';
      color: #fff;
      text-transform: uppercase;
      font-size: 5rem;
      line-height: 5rem;
      margin-bottom: 1.5rem;
    }
  }

  .category{
    margin-bottom: 1.5rem;
  }

  .category-title{
    font-size: 1.8rem;
    line-height: 1.8rem;
    font-family: 'Gotham Bold';
    text-transform: uppercase;
    padding-bottom: 0.8rem;
    max-width: 27rem;
    border-bottom: 4px solid #f5f5f5;
  }

  .category-description{
    max-width: 66rem;
    font-family: 'Gotham Book';
    font-size: 1.6rem;
    margin-bottom: 8rem;
  }

  .bike{
    margin-bottom: 6rem;
    .bike-image{
      margin-bottom: 3rem;
      cursor: crosshair;
      img{
        width: 100%;
      } 
    }

    .bike-title{
      margin-bottom: 1.5rem;
      a{
        color: #333;
        font-family: 'DIN CondensedBold';
        font-size: 3rem;
        line-height: 3rem;
      } 
    }

    .bike-brand{
      font-family: 'Gotham Book';
      font-size: 1.6rem;
    }
  } 
}

#{$viewport-md-down}{
  .bikes{
    .featured .featured-content{
      max-width: none;
      margin: 0;
    }
  }
}